.section-products__sub-title {
  margin-bottom: 15px;
}
.section-products__title {
  margin-bottom: 144px;

  @media screen and (min-width: 768px) {
    margin-bottom: 150px;
  }
  @media screen and (min-width: 1200px) {
    margin-bottom: 260px;
  }
}

.products-list {
  @include list;
  @media screen and (min-width: 768px) {
    display: flex;
    margin: -15px;
  }
}
.products-list__card {
  max-width: 480px;
  margin: 0 auto;
  padding-top: 161px;
  padding-bottom: 40px;

  border-radius: 24px;
  text-align: center;

  @media screen and (min-width: 768px) {
    max-width: 220px;
  }
  @media screen and (min-width: 768px) {
    max-width: 330px;
  }
}

.products-list__item {
  position: relative;

  &:not(:last-child) {
    margin-bottom: 130px;
    @media screen and (min-width: 768px) {
      margin-bottom: 0;
    }
  }

  @media screen and (min-width: 768px) {
    width: calc((100% - 40px) / 3);

    margin: 10px;
  }

  &:nth-child(1) .card {
    background-color: pink;
    &::before {
      position: absolute;

      top: -113px;
      left: 50%;
      transform: translateX(-50%);

      content: ' ';
      display: inline-block;
      width: 249px;
      height: 252px;
      background-image: url(../images/products1.png);
      @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
        background-image: url(../images/products1@2x.png);
        background-size: cover;
      }
      @media screen and (min-width: 768px) {
        top: -104px;
        width: 220px;
        height: 226px;
        background-image: url(../images/products1tablet.png);

        @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
          background-image: url(../images/products1tablet@2x.png);
        }
      }
      @media screen and (min-width: 1200px) {
        top: -210px;

        width: 335px;
        height: 335px;
        background-image: url(../images/products1desktop.png);

        @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
          background-image: url(../images/products1desktop@2x.png);
        }
      }
    }
  }
  &:nth-child(2) .card {
    background-color: #f0d1a5;
    &::before {
      position: absolute;

      top: -113px;
      left: 50%;
      transform: translateX(-50%);

      content: ' ';
      display: inline-block;
      width: 249px;
      height: 252px;
      background-image: url(../images/products2.png);
      @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
        background-image: url(../images/products2@2x.png);
        background-size: cover;
      }
      @media screen and (min-width: 768px) {
        top: -111px;
        width: 220px;
        height: 233px;
        background-image: url(../images/products2tablet.png);
        background-size: cover;

        @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
          background-image: url(../images/products2tablet@2x.png);
          background-size: cover;
        }
      }
      @media screen and (min-width: 1200px) {
        top: -210px;
        width: 337px;
        height: 335px;
        background-image: url(../images/products2desktop.png);
        background-size: cover;

        @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
          background-image: url(../images/products2desktop@2x.png);
          background-size: cover;
        }
      }
    }
  }
  &:nth-child(3) .card {
    background-color: #c2e297;
    &::before {
      position: absolute;

      top: -113px;
      left: 50%;
      transform: translateX(-50%);

      content: ' ';
      display: inline-block;
      width: 249px;
      height: 252px;
      background-image: url(../images/products3.png);
      @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
        background-image: url(../images/products3@2x.png);
        background-size: cover;
      }
      @media screen and (min-width: 768px) {
        top: -102px;
        width: 220px;
        height: 224px;
        background-image: url(../images/products3tablet.png);
        background-size: cover;

        @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
          background-image: url(../images/products3tablet@2x.png);
          background-size: cover;
        }
      }
      @media screen and (min-width: 1200px) {
        top: -210px;
        width: 335px;
        height: 335px;
        background-image: url(../images/products3desktop.png);
        background-size: cover;

        @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
          background-image: url(../images/products3desktop@2x.png);
          background-size: cover;
        }
      }
    }
  }
}

.products-list__title {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 1.15;

  margin-bottom: 30px;

  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: #ffffff;
}

.products-list__text-container {
  margin: 0 auto;
  padding: 0 39px;
  max-width: 280px;

  &::before {
    content: '';

    margin-bottom: 30px;

    display: inline-block;
    width: 26px;
    height: 6px;

    background-image: url(../images/title-after.png);
  }

  @media screen and (min-width: 768px) {
    padding: 0 10px;
  }
}

.products-list__text {
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  text-align: center;

  color: #ffffff;
}

.products-list__button {
  margin-top: 30px;

  width: 40px;
  height: 40px;

  background-color: white;
  border: 0 solid;
  border-radius: 50%;

  cursor: pointer;

  background-image: url(../images/arrow-right.png);

  @media screen and (min-width: 768px) {
    margin-top: 40px;
  }
}
